

.d-main {
  height: 95vh;
  display: flex;
  justify-content: center;
  align-items: center;
}
.d-main .box {
  padding: 2vh;
  box-shadow: rgba(0, 0, 0, 0.25) 0px 54px 55px, rgba(0, 0, 0, 0.12) 0px -12px 30px, rgba(0, 0, 0, 0.12) 0px 4px 6px, rgba(0, 0, 0, 0.17) 0px 12px 13px, rgba(0, 0, 0, 0.09) 0px -3px 5px;
  gap: 6vh;
  height: 50%;
  width: 50%;

  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
.d-main .box .image{
  display: flex;
  justify-content: center;
}
.d-main .box img{
  border-bottom: 2px solid black;
  border-radius: 70px;
  /* height: 100%; */
  width: 50%;

}
.data{
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  gap: 2vh;
}
.data input{
  /* width:; */
  /* margin-top:px; */
  padding: 4px;
  margin: auto;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
  height: 30px;

}
.main{
  display: flex;
  justify-content: center;
}
.login{
  height: 100vh;
  display: flex;
  justify-content: center;
  flex-direction: column;
}
.users{
  display: flex;
  justify-content: center;
}
.list{
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  width: 50%;
  margin-top: 4vh;
  padding: 2vh;
  background-color: white;
  box-shadow: rgba(0, 0, 0, 0.1) 0px 4px 12px;
}

.userNames{
  margin-top: 4vh;
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  justify-content: center;
  align-items: center;
  gap: 2vh;
}